import Link from 'next/link';
import { Calculator, Heart } from 'lucide-react';

/**
 * Site Footer Component
 * Displays navigation links, copyright, and branding
 */
export default function Footer() {
    const currentYear = new Date().getFullYear();

    const footerLinks = [
        { href: '/salary', label: '' },
        { href: '/tax', label: '' },
        { href: '/mortgage', label: '' },
        { href: '/capital', label: '' },
        { href: '/articles', label: '' },
    ];

    const legalLinks = [
        { href: '/privacy', label: '' },
        { href: '/terms', label: '' },
        { href: '/developers', label: ' API' },
    ];

    return (
        <footer className="bg-white/80 backdrop-blur-md border-t border-slate-100 mt-auto">
            <div className="max-w-6xl mx-auto px-4 sm:px-6 py-12">
                {/* Main Footer Content */}
                <div className="grid grid-cols-1 md:grid-cols-4 gap-8">
                    {/* Brand */}
                    <div className="col-span-1 md:col-span-2">
                        <Link href="/" className="flex items-center space-x-2 group mb-4">
                            <div className="w-8 h-8 bg-gradient-to-br from-indigo-500 to-purple-600 rounded-lg flex items-center justify-center text-white">
                                <Calculator className="w-4 h-4" />
                            </div>
                            <span className="text-lg font-bold text-slate-800">TaiCalc</span>
                        </Link>
                        <p className="text-slate-500 text-sm leading-relaxed max-w-sm">
                            3 
                        </p>
                    </div>

                    {/* Quick Links */}
                    <div>
                        <h3 className="text-sm font-semibold text-slate-800 mb-4"></h3>
                        <ul className="space-y-2">
                            {footerLinks.map((link) => (
                                <li key={link.href}>
                                    <Link
                                        href={link.href}
                                        className="text-sm text-slate-500 hover:text-indigo-600 transition-colors"
                                    >
                                        {link.label}
                                    </Link>
                                </li>
                            ))}
                        </ul>
                    </div>

                    {/* Legal */}
                    <div>
                        <h3 className="text-sm font-semibold text-slate-800 mb-4"></h3>
                        <ul className="space-y-2">
                            {legalLinks.map((link) => (
                                <li key={link.href}>
                                    <Link
                                        href={link.href}
                                        className="text-sm text-slate-500 hover:text-indigo-600 transition-colors"
                                    >
                                        {link.label}
                                    </Link>
                                </li>
                            ))}
                        </ul>
                    </div>
                </div>

                {/* Bottom Bar */}
                <div className="mt-12 pt-8 border-t border-slate-100 flex flex-col sm:flex-row items-center justify-between gap-4">
                    <p className="text-xs text-slate-400">
                         {currentYear} TaiCalc . All rights reserved.
                    </p>
                    <p className="text-xs text-slate-400 flex items-center gap-1">
                        Made with <Heart className="w-3 h-3 text-rose-400" /> in Taiwan
                    </p>
                </div>
            </div>
        </footer>
    );
}
